body, html {
    margin: 0;
    padding: 0;
    background-color: #020510;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 14px;
    line-height: 1;
    height: 100%;
    -ms-overflow-style: none; /* 完全隐藏滚动条 */
    scrollbar-width: none; /* 完全隐藏滚动条 */
    
}
::-webkit-scrollbar {
    display: none; /* 完全隐藏滚动条 */
}

.home-content{
    overflow: hidden; /* Prevents scrolling */  
    
}

a{
    color: inherit;
    text-decoration: none;

}

.header {
    width: 100%;
    height: 8%;
    background-color: #020510;
    display: flex;
    justify-content:space-between;
    align-items: center; 
}

.menu-icon {
    display: none;
    /* 为菜单图标添加其他需要的样式 */
}

.menu-icon img{
    height: 40px !important;
    width: 40px;
}
.close-icon {
    color: white;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif ;
    font-size: 2rem;
    position: absolute;
    top: 0;
    right: 0;
    padding: 1em;
    /* 添加其他你需要的样式，比如大小、颜色等 */
}
.mobile-nav {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #020510;
    flex-direction: column;
    z-index: 1000;
}

.header-right {
    width:100%;
    display: flex;
    justify-content: right;
    align-items: center;
}

.header img{
    margin-right: 20px;
    margin-left: 20px;
    height: 53px;
}

.desktop-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    cursor: pointer;
}

#nav {
    margin-left: 5%;
    width: 80%;
    font-size: 1.1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    color: rgb(235, 235, 235);
}

#nav p{
    cursor: pointer;
    position: relative;
}
#nav p:hover {   
    color: #ff2b78;
    margin-top: 16px;
    font-weight: 400;
    line-height: 4.5rem;
    transition: color 0.5s ease;
}
#nav p::before {
    content: "";
    position: absolute;
    bottom: 0; /* 位于p的底部 */
    left: 50%; /* 从中间开始 */
    transform: translateX(-50%); /* 确保它真正从中心开始 */
    width: 0; /* 初始宽度为0 */
    height: 3px; /* 和你的border-bottom一样高 */
    background-color: #ff2b78;
    transition: width 0.3s ease; /* 这里的过渡效果是宽度变化 */
}
#nav p:hover::before {
    width: 100%; /* 鼠标悬停时，宽度变为100%，从而实现从中间向外的效果 */
}

.underLine{
    height: 1px;
    width: 80%;
    border-bottom: #464751 1px solid;
}

.search {
    height: 36px;
    width: 20%;
    border-radius: 6px;
    border: 1px #464751 solid;
    background-color: #020510;
    padding: 10px;
    border: 1px solid #ccc;
    box-sizing: border-box; /* 边框包括在总宽度和高度内 */
    color: white;
}

