h1 {
    font-size: 5.6rem;
    line-height: 1.2;
    margin: 0;
    margin-bottom: 3px;
    animation: glow 2.5s infinite alternate; /* infinite 无线循环， alternate 动画结束时反转 */
}

/* glow animate */
@keyframes glow {
    0% {
        text-shadow: 0 0 5px #ff2b78;
    }
    50% {
        text-shadow: 0 0 10px #ff2b78;
    }
    100% {
        text-shadow: 0 0 5px #ff2b78;
