.introduction {
    text-align: center;
    margin-top: 20px;
}

.right-section {
    max-height: calc(100vh - 350px); /*100vh 表示的是浏览器内部窗口（视口）的100%高度。 调整100px为你的头部和页脚总高度 */
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow-y: auto; /* Allows scrolling within this section if needed */
    scrollbar-width: none; /* 针对 Firefox 隐藏滚动条 */
}

.right-section::-webkit-scrollbar {
    display: none; /* 针对 Chrome 和 Safari 隐藏滚动条 */
}

.image-container {
    position: relative;
    margin-left: 18px;
    margin-bottom: 48px;
    width: calc(18% - 10px);
}

.image-container img {
    border-radius: 20px;
    width: 100%;
    height: auto;
    cursor: pointer;   
}

.image-container img:hover {
    filter: brightness(120%);
    transition: filter 0.3s ease;
}

.image-caption {
    position: absolute;
    bottom: 0;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* 半透明黑色背景 */
    color: white;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    padding: 10px;
    box-sizing: border-box;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
}

/* Media Query for Mobile Devices */
@media only screen and (max-width: 768px) {
    .content {
        display: flex;
        flex-direction: column; /* Stack the left and right sections vertically */
    }
    .left-section {
        width: 100%;
        order: 1; /* Places the left section in the middle */
    }
    .right-section {

        align-items: center; /* Center items horizontally */
        max-height: 100vh;
        width: 100%;
        order: 2; /* Places the right section at the bottom */
        padding: 0px;
        overflow-y: auto; /* Allow vertical scrolling */
        scrollbar-width: none; /* Hide scrollbar in Firefox */
    }
    .right-section::-webkit-scrollbar {
        display: none; /* Hide scrollbar in Chrome and Safari */
    }
    .image-container {
        width: 40%; /* Take most of the width in mobile view */

        margin-bottom: 10px; /* Reduce bottom margin */
    }
    .right-section img {
        width: 100%; /* Ensure images take full width of container */
        height: auto; /* Maintain aspect ratio */
    }
}




